<style lang="less" scoped>
.container{
  background-color: #eee;
}
// 头部
.header {
  display: flex;
  height: 100px;
  background-color: #fff;
  div {
    flex: 2;
    padding-left: 20px;
    height: 100px;
  }
  span {
    flex: 3.333;
    display: inline-block;
    text-align: center;
    height: 100px;
    line-height: 100px;
    &:nth-child(1) {
      color: #999;
      text-align: left;
      text-indent: 1em;
      font-size: 25px;
    }
    &:nth-child(2) {
      font-size: 40px;
    }
    &:nth-child(3) {
      text-align: right;
      padding-right: 1em;
      color: #44b336;
      box-sizing: border-box;
      font-size: 25px;
    }
  }
} // 头部
.nickName{
  width: 100%;
  padding: 20px 0;
  box-sizing: border-box;
  position: relative;
  input{
    outline: none;
    border: 0;
    width: 100%;
    height: 80px;
    text-indent: 1em;
    font-size: 30px;
    padding-right: 2.5rem;
    box-sizing: border-box;
  }
  span{
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    display: inline-block;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #aaa;
    color: #fff;
    text-align: center;
    line-height: 30px;
    cursor: pointer;
  }
}
</style>

<template>
	<div class="container">
			<div class="header">
				<span @click="back">取消</span>
				<span>修改昵称</span>
        <span @click="sure(user_info)">确定</span>
		  </div>
      <div class="nickName" :id="params.userId" :alt="params.phone">
        <input type="text" v-model="params.nickName" placeholder="请输入您的昵称" ref="nickName">
        <span @click="clean">×</span>
      </div>
	</div>
</template>
<script>
export default {
  data () {
    return {
      nickName:null,
      params:{
        phone:null,
        nickName:null,
        userId:null
      },
      user_info:{}
    }
  },
  methods: {
    back() {
      this.$router.go(-1)
    },
    sure(user_info) {
      this.$Indicator.open({
        text: '修改中...',
        spinnerType: 'fading-circle'
      })
      this.axios.post('/api/changeName', this.params).then(data =>{
        this.$store.state.userModule.LOGIN_USER_OBJECT.user.nickname =  this.params.nickName
        this.$store.state.userModule.USER_INFO_OBJECT.nickname =  this.params.nickName
        if(data.data.result){
          this.$Indicator.close()
          this.$Toast({
            message: '修改成功',
            position: 'middle',
            duration: 1000
          })
          setTimeout(()=>{
            this.$router.push({
              name: 'baseInfo',
              params:{
                name:this.$route.params.name
              }
            })
          }, 1000)
        }
      }).catch((err)=>{
        console.log(err)
      });
    },
    clean(){
      this.params.nickName = null
      this.$nextTick(function () {
        this.$refs.nickName.focus()
      })
    }
  },
  mounted () {
    this.params.userId  = this.$route.params.id
    this.params.phone = this.$route.params.phone
    this.params.nickName = this.$route.params.name
  }
};
</script>



